<template>
	<view class="user-bank">
		<view class="bank">
			<text class="tit">{{ title }}</text>
			<template v-if="step == 1">
				<view class="input-wrap row verCenter boxsiz"><input type="text" value="" placeholder="持卡人姓名" placeholder-class="input-placeholder" /></view>
				<view class="input-wrap row verCenter boxsiz"><input type="text" value="" placeholder="卡号" placeholder-class="input-placeholder" /></view>
				<view class="btn" @click="next()">下一步</view>
			</template>
			<template v-else-if="step == 2">
				<view class="input-wrap row verCenter bothSide boxsiz">
					<view class="bank-txt row verCenter">
						<image src="https://golemo.oss-cn-shenzhen.aliyuncs.com//5f3246ffad6d5.jpg" mode="aspectFill"></image>
						<text>中国银行储蓄卡</text>
					</view>
					<icon class="ico-1"></icon>
				</view>
				<view class="input-wrap row verCenter boxsiz"><input type="text" value="" placeholder="银行预留手机号" placeholder-class="input-placeholder" /></view>
				<view class="input-wrap row verCenter bothSide">
					<text class="txt">收不到验证码？</text>
					<input type="text" value="" placeholder="验证码" placeholder-class="input-placeholder" />
					<button class="code">获取验证码</button>
				</view>
				<view class="btn" @click="submit()">提交</view>
			</template>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			step: 2,
			title: '添加银行卡'
		};
	},
	methods:{
		next(){
			this.title="银行卡信息";
			this.step=1;
		},
		submit(){
			
		}
	}
};
</script>

<style  lang="scss">
@import '../../assets/css/user/bank.scss';
</style>
